// ＜rem＞可変フォント pc 1024~1919
// ===========================================================
// ===========================================================
@function variableFontPc($upper, $lower) {
  $max: $upper;
  $min: $lower;
  $viewMin: calc(1024 / 100) + px;
  $diff: calc($max - $min);
  $viewPxDiff: calc(1919 - 1024);
  $ratio: calc((($diff * 10) / $viewPxDiff) * 100);

  @return calc(#{$min}rem + ((1vw - #{$viewMin})) * #{$ratio});
}

// $min:指定したい文字サイズの最小値
